/* Media Queries */
*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.w-5 {
  width: 5rem !important;
}

.w-15 {
  width: 15rem !important;
}

.perfil {
  width: 5rem;
  border-radius: 3rem;
  margin-right: 1rem;
}

.layout {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .layout {
    display: grid;
    grid-template-columns: 40% 60%;
    min-height: 100vh;
  }
}
.layout__app {
  width: min(95%, 60rem);
  margin: 0 auto;
  padding: 3rem 0;
}
.layout__imagen {
  height: 20rem;
  background-image: image-set(url("/build/img/layout.jpg") 1x, url("/build/img/layout.webp") 1x);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .layout__imagen {
    height: auto;
  }
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #1E293B;
  padding: 2rem;
}
@media (min-width: 768px) {
  .dashboard__mobile {
    display: none;
  }
}
.dashboard__tablet {
  display: none;
}
@media (min-width: 768px) {
  .dashboard__tablet {
    display: block;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  margin: 0;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto; /* para que se quede estatico mientras el contenido avanza */
}
.dashboard__sidebar {
  display: none;
}
@media (min-width: 768px) {
  .dashboard__sidebar {
    display: block;
    background-color: #2f405d;
    width: 15rem;
    min-height: 100vh;
  }
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 3rem 3rem 0 3rem;
  }
}