@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$gris-claro;

    &__header {
        background-color: v.$gris-oscuro;
        padding: 2rem;
    }

    &__mobile {
        @include m.tablet {
            display: none;
        }
    }

    &__tablet {
        display: none;
        @include m.tablet {
            display: block;
        }
    }

    &__logo {
        color: v.$blanco;
        margin: 0;
    }

    &__grid {
        display: flex;
        flex: 1;
        overflow-y: auto; /* para que se quede estatico mientras el contenido avanza */
    }

    &__sidebar {
        display: none;

        @include m.tablet {
            display: block;
            background-color: lighten(v.$gris-oscuro, 10%);
            width: 15rem;
            // height: calc(100vh - 9rem);
            // height: 100vh;
            min-height: 100vh;
        }
    }

    &__menu {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 3rem;
    }

    &__contenido {
        width: 100%;
        padding: 1rem;
        // overflow-y: scroll; /* arriba abajo */

        @include m.tablet {
            padding: 3rem 3rem 0 3rem;
        }
    }
}